






















import Vue from 'vue';
import {Component} from 'vue-property-decorator';

@Component
export default class NumberPad extends Vue {
  output  = '0';  // 面板输出结果

  //获取用户点击内容
  inputContent(event: MouseEvent){
    const Button = event.target as HTMLButtonElement;
    const input = Button.textContent as string  //input 是用户点击得到的字符

    if(this.output === '0'){ //当面板输出为 0 时
      if(input === '0'){
        return
      }else if('0123456789'.indexOf(input) >= 0){ //如果用户点击的是 '123456789' 中的一个
        this.output = input;
      }else{
        this.output += input;
      }
      return;
    }

    if(this.output.indexOf('.')>=0){  //当面板有'.'时，用户不能再输入'.'
      if(input === '.'){
        return;
      }
    }

    this.output += input;
  }

  //删除
  remove(){
    if(this.output.length === 1){
      this.output = '0'
    }else {
      this.output = this.output.slice(0,-1)
    }
  }

  //清空
  clear(){
    this.output='0'
  }

  ok(){
    const number = parseFloat(this.output)
    this.$emit('update:value',number) //将本组件的金额收到money页面
    this.$emit('submit', number)  //提交用户存储一次的数据record
    this.output = '0'
  }
}
